import {mapState} from 'vuex';
export default {
    name: "map",
    data() {
        return {
            config: null,
        };
    },
    onLoad() { this.$commonLoad.onload();
        this.loadData();
    },
    computed: {

        polygons() {
            let polygons = [];
            if (this.config && this.config.range && this.config.range.length > 0) {
                let points = [];
                for (let i in this.config.range) {
                    points.push({
                        latitude: this.config.range[i].lat,
                        longitude: this.config.range[i].lng,
                    });
                }
                polygons.push({
                    points: points,

                    strokeColor: '#4d77ff',
                    strokeWidth: 2,





                    fillColor: '#4d77ff40',
                });
            }
            return polygons;
        },






















        markers() {
            let markers = [];
            if (this.config && this.config.range && this.config.address) {
                markers.push({
                    latitude: this.config.address.latitude,
                    longitude: this.config.address.longitude,

                    callout: {
                        content: this.mall.name,
                        color: '#353535',
                        bgColor: '#ffffff',
                        display: 'ALWAYS',
                        fontSize: 13,
                        padding: 4,
                        borderWidth: 2,
                        borderRadius: 10,
                        borderColor: '#ffffff'
                    },
                    anchor: {
                        x: 0.5,
                        y: 1
                    },













                    iconPath: 'https://yewi.vvv6g.cn/web/mini_images/location.png',
                    width: 20,
                    height: 20,
                })
            }
            return markers;
        },
        ...mapState({
            mall: state => state.mallConfig.mall,
            iPhoneX: state => state.iPhoneX
        }),
    },
    methods: {
        loadData() {
            this.$request({
                url: this.$api.order.delivery,
                method: 'post',
            }).then(response => {
                if (response.code == 0) {
                    this.config = response.data.config;
                } else {
                    uni.showModal({
                        content: response.msg,
                        showCancel: false
                    });
                }
            })
        },
        mobile() {
            uni.makePhoneCall({
                phoneNumber: this.config.contact_way,
            })
        }
    }
}
